/*设置遮罩层*/
.modalCover{
    display: none;/*初始不显示*/
    width: 100%;
    height: 100%;
    position: fixed; /*fixed+高宽能保证无论拉动滚动条位置都不会发生变化*/
    top: 0;
    left: 0; /*top+left保证能从最左上角开始*/
    background: #222; /*设置颜色*/
    opacity: 0.7;/*透明级别0不透明1完全透明*/
    /*background + opacity 能保证将背景色透明化*/
}
.modal{
    border-radius: 2px;
    position: absolute;
    width: 400px;
    height: 300px;
    margin-top: 10%;
    left: 50%;
    margin-left: -200px;
    background: #fff;
    border:1px solid #ddd;
}


.checkMaskCss:checked + .modalCover{
    display: block;
}
.close{
    position: absolute;
    right: 5px;
    top: 0px;
    cursor: pointer;
}

.myTitle {
    color: red;
}


