.BackWall {
    background: rgba(4,4,7,0.82);
    opacity: 0.7;
    width: 100vw;
    height: 100vh;
    position :fixed;
}
.leftResumeCss {
    position :fixed;
    left : 0;
    top: 0;
    width: 50vw;
    height: 80vh;
    background: #dddddd;
    -webkit-animation : moveDiv 2s linear ;
            animation : moveDiv 2s linear ;
    -webkit-animation-delay : 4s;
            animation-delay : 4s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}

@-webkit-keyframes moveDiv {
