h1 {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 4.5em;
    margin: auto;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: typing 3s steps(26, end),
    cursor-blink 0.3s step-end infinite alternate;
            animation: typing 3s steps(26, end),
    cursor-blink 0.3s step-end infinite alternate;
}

@-webkit-keyframes typing {
    from {
        width: 0;
    }
}

@keyframes typing {
    from {
        width: 0;
    }
}

@-webkit-keyframes cursor-blink {
    50% {
       /* border-color: transparent;*/
    }
}

@keyframes cursor-blink {
    50% {
       /* border-color: transparent;*/
    }
}

.personMessage {
    position: absolute;
    top: 3.5rem;
    right: 8rem;
}

.resumeToggleCss {
    display : -ms-flexbox;
    display : flex;
    position : absolute;
    top: 7.5rem;
    width : 98%;
    -ms-flex-wrap : wrap;
        flex-wrap : wrap;
    -webkit-animation : fromHideToShow 6s linear ;
            animation : fromHideToShow 6s linear ;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}

@-webkit-keyframes fromHideToShow {
    from {
        opacity: 0;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fromHideToShow {
    from {
        opacity: 0;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
    }
}


